body {
  font-family: "Century Gothic", sans-serif;
  --orange: #f79812;
  --orangeOpaque: rgba(247, 152, 18, 0.85);
  --red: #ff0000;
  --redOpaque: rgba(235, 87, 87, 0.35);
  --green: #6fcf97;
  --greenOpaque: rgba(33, 150, 83, 0.2);
  --blue: #2d9cdb;
  --blueOpaque: rgba(47, 128, 237, 0.35);
  --darkBlue: #595959;
  --gray: rgba(130, 130, 130, 0.3);
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 175px;
  flex-direction: column;
}

.form__container {
  top: 175;
  flex-direction: column;
  width: 487px;
  height: 319px;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.2);
}

.google__container {
  display: flex;
  width: 424px;
  height: 40px;
  border: 1px solid #d9d9d9;
  align-items: center;
  justify-content: center;
}

.google__logo {
  object-fit: contain;
  margin-right: 10px;
}

.signInPage__buton {
  /* background-color: var(--orangeOpaque); */
  color: gray;
  border-radius: 5px;
  border: 0;
}
.button:hover {
  color: white;
  border-color: var(--orange);
  background-color: var(--orange);
}
.button:focus {
  color: white;
  border-color: var(--orange);
  background-color: var(--orange);
}
